.main {
  min-height: 900px;
  font: 100%/1 "Open Sans", Helvetica, Arial, sans-serif;
  font-weight: 300;
  min-width: 20em;
  margin: 0 auto;
  color: black;
  background-image: url("../../../src/assets/images/world-map-vector.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.search_input {
  -webkit-appearance: none;
  position: relative;
  border: 0;
  border-bottom: 1px solid #fff;
  background: transparent;
  font: inherit;
  font-size: 22px;
  color: #fff;
  width: 40%;
  padding: 0 4px;
  border-radius: 0;
  text-align: center;
}

.search_input:focus {
  outline: none;
}

.container-fluid{
    padding: 0;
    text-align: center;
}

.searchbar{
    font-size: xx-large;
    background-color: #263238;
    color: #ffffff;
    padding: 20px;
}

.main-content {
  padding: 20px;
}


.inactive {
  -webkit-filter: grayscale(100%) blur(5px); 
  filter: grayscale(100%) blur(5px);
}

.result{
    padding: 20px;
}

input{
    height: 35px;
}

input[type=text] {
    width: 270px;
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
}
  
input[type=text]:focus {
    width: 300px;
}

li{
    list-style-type:none;
}
.list{
    position: fixed;
    margin-top: -60px;
    left: 65.2%;
    z-index: 1;
}
button{ 
    font-size: large;
    margin-top: 1px;
    width: 200px;
    background-color: white;
    border: 1px solid gray;
    text-align: left;
}
button:hover{
    font-weight: 500;
}

.row {
    display: flex;
    justify-content: space-around;
    margin: 0;
}
.row div{
    margin: 5px 0;
}

.madeIn{
    width: 150px;
    padding: 20px;
    background-color: #dee4e7;
    font-size: large;
    border-radius: 20px;
    border: 10px solid white;
}

.footer{
    position: absolute;
    bottom: 20px;
    left: 20px;

    padding: 0 5% 1.875em;
    min-width: 20em;
    max-width: 81.25em;
    margin: 0 auto;
}
.footer a{
    color: #fff;
    text-align: left;
    display: block;
}

.footer a:hover {
  color: #fff;
}

.footer-link{
    color: #fff;
    text-align: left;
    display: block;
}

.footer-link:hover {
  color: #fff;
}

.share-button{
    width: auto;
}

@media only screen and (max-width: 600px) {
    .list{
            position: fixed;
            margin-top: -25px;
            left: 13%;
    }
  }